.Player {
    background-color: #E95B51;
    padding: 7px 12px;
    font-family: 'Krub', sans-serif;
    font-size: 29px;
    border-radius: 7px;
    margin-bottom: 7px;

    display: flex;
}

.Player.Add, .Add #PlayerControl button, .Add #PlayerControl {
    background-color: #81BF40;
    border: 0px;
    font-family: 'Krub', sans-serif;
    font-size: 27px;
    margin-left: 7px;
}

#removeBtn {
    background-color: #E95B51;
    border: 0px;
    font-family: 'Krub', sans-serif;
    font-size: 27px;
    margin-left: 7px;
}

#namePlay {
    flex-grow: 1;
    display:inline-block;
    margin-left: 25px;
}
#nameSettings {
    flex-grow: 1;
    display:inline-block;
    margin-left: 5px;
}

#score {
    min-width: 35px;
}

.PlayerControl {
    font-family: 'Krub', sans-serif;
    font-size: 27px;
    background-color: #E95B51;
    margin-left: 7px;
    border: 0px;
}

#PlayerControl button {
    cursor: pointer;
}

input {
    font-family: 'Krub', sans-serif;
    font-size: 27px;
    border-radius: 7px;
    border: 0px;
    width: 100%;
}
